import React, { Component } from 'react';
import './Editor.css';
import Profile from './Profile';
import Article from './Article';
import Card from './Card';
import getEmbedly from './EmbedlyDao';

class Editor extends Component {
  constructor(props){
    super(props);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.onPaste = this.onPaste.bind(this);
    this.editorChange = this.editorChange.bind(this);
    this.getCard = this.getCard.bind(this);
    this.hasValue = this.hasValue.bind(this);
    this.detectURL = this.detectURL.bind(this);
    this.state ={
      embedlyUrl : undefined,
      content : undefined,
      cardInfo : undefined
    }
  }
getForcedState(embedlyUrl,content){
  //Promises .
  return new Promise(resolve=>{
    //URL   
    if(embedlyUrl){
      //Embedly API  response.data  .
      getEmbedly(embedlyUrl).then((response)=>{
        console.log(response.data);
        let cardInfo = Object.assign({},response.data);
        //setState    
        resolve({
          embedlyUrl : embedlyUrl,
          content : content,
          cardInfo : cardInfo
        });
      }).catch((error)=>{
        resolve({
          embedlyUrl : undefined,
          content : undefined,
          cardInfo : undefined
        });
      });
    }else{
      //URL     state content     bypass  
      //  setState       
      //  ,   
      resolve({
        embedlyUrl : undefined,
        content : content,
        cardInfo : undefined
      });
    }
  })
}
 onPaste(event){
  event.clipboardData.items[0].getAsString(text=>{
    let checkText = this.detectURL(text);
    //   URL     .
    //state    .
    if(checkText){
      this.getForcedState(checkText).then((obj)=>{
        this.setState(obj);
       });
    }
  })
}
 editorChange(event){
    let checkText = this.detectURL(event.currentTarget.textContent);
    if(!this.state.embedlyUrl&&
        (event.keyCode===32||event.keyCode===13)&&
        checkText){
      this.getForcedState(checkText,event.currentTarget.textContent)
          .then((obj)=>{
            this.setState(obj);
          });
    }else{
      this.getForcedState(undefined,event.currentTarget.textContent)
          .then((obj)=>{
            this.setState(obj);
          });
    }
  }
  getCard(embedlyUrl){
    if(embedlyUrl){
      return(
        <div>{embedlyUrl}</div>
      );
   }else{
      return(<div/>);
    }
  }
  hasValue(value){
    if((value && (typeof value) === "string"))
      return (!value)?false:(value.trim()===""?false:true);
    else return false;
  }
  handleSubmit(event){
    this.getForcedState(this.detectURL(this.state.content), this.state.content)
      .then((obj)=> {
        this.setState(obj);
          let article = Object.assign({}, Article());
  article.user = "Genji";
  article.content = this.state.content;
  article.cardInfo = this.state.cardInfo;
        this.props.handleSubmit(article);
        this.setState({
      embedlyUrl : undefined,
      content : undefined,
      cardInfo : undefined
    });
      });

}
  detectURL(text){
    var urls = text.match(/(https?:\/\/[^\s]+)/g)||text.match(/(www.[^\s]+)/g);
    if(urls){
      if(urls.length>0) return urls[0];

    }

    else{
       return undefined;
    }

  }
  render() {
    return (
      <div className="wrapEditor">
        <Profile isAnonymous={this.props.isAnonymous}/>
        <div className="textEditor">
          <div className="innerEdit"
            contentEditable="true"
            placeholder="..."
            onPaste={this.onPaste}
      onKeyUp={this.editorChange}
        ></div>
        <Card cardInfo={this.state.cardInfo}/>
        </div>
        <div className="actionBar">
          <button className="upload"
            disabled={!this.hasValue(this.state.content)}
            onClick={this.handleSubmit}><span>!</span></button>
        </div>
      </div>
    );
  }}
export default Editor;



